.Advisor-main{
    display: flex;
    height: 300vh;
    /* margin:  0 auto;
    margin-left: 1vw; */
    background-color: #f7f7f7;
}
.Advisorpitch-row-flex{
    display: flex;
    width: 100%;
    margin-top: 4vw;
    margin-bottom: 10vw;
    margin-left: 2vw;
    margin-right: 2vw;
    padding-left: 1vw;
    padding-right: 1vw;
    background-color: #fff;
    }
.Advisorpitch-details-card-style {
    overflow: hidden;
    }
  .Advisorpitch-details-card-content-align {
    margin-left: 1vw;
    margin-right: 1vw;
    margin-top: 1.3vw;
    margin-bottom: 1.3vw;
  }
  .success-head{
      display: flex;
      font-size: 15px !important;
      font-weight: bold !important;
}
.success-heading1{
    display: flex;
    text-align: justify;
    font-size: 14px !important;
    font-weight: bold !important; 
}
.success-story{
    margin: 0 auto;
    padding: 20px 20px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow:  1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.success-story-form{
    display: flex;
    flex-wrap: wrap;
    gap:0 10px;
}
.success-story-form>.ant-col{
    flex:0 1 49%;

}
.about-heading{
display: flex;
font-size: 15px;
font-weight: bold;
font-family: 'robotoregular';
}
.about-paragraph{
    display: flex;
    font-size: 14px;
    font-family: 'robotoregular';
    padding-top: 10px;
    color: #565656;
    line-height: 2.0;
    text-align: justify;
}
.about-head2{
display: flex;
font-size: 15px;
font-weight: bold;
}
.process{
    font-size: 14px;
    color: #9d9d9d;
    display: flex;
}
.success-comments{
    font-size: 14px;
    color: #9d9d9d;
    display: flex;
    text-align: justify;
}

@media (max-width:375px) {
    .success-story-form>.ant-col{
        flex:0 1 100%;
        
    }
    .Advisor-main{
        display: flex;
        flex-direction: column;
        margin-left: 1vw;
        margin-right: 1vw;
        background-color: #f7f7f7;
    }
    .about-heading{
        display: flex;
        flex-direction: column;
        margin-top: 1vw;
        margin-left: 1vw;
        font-size: 15px;
        text-align: start;
        font-family: 'robotoregular';
    }
    .about-paragraph{
        display: flex;
        flex-direction: column;
        font-size: 14px;
        font-family: 'robotoregular';
        text-align: justify;
        margin-left: 2vw;
        margin-right: 2vw;
        line-height: 1.7;
        /* text-align: justify; */
    }
    .about-head2{
        display: flex;
        flex-direction: column;
        font-size: 14px;
        font-family: 'robotoregular';
        text-align: start;
        /* line-height: 2.0; */
    }
    
    .process{
display: flex;
font-size: 14px;
    }
    
    .success-head{
        display: flex;
        font-size: 14px !important;
    }
    .success-comments{
        display: flex;
        text-align: justify;
        font-size: 14px !important;
    }
    .success-heading1{
        display: flex;
        text-align: justify;
        font-size: 15px !important;
    }
}